footer {
    background: black;
    padding: 100px 0px;
    .container {
        .footer__nav {
            display: flex;
            justify-content: space-between;
            .college-info {
                img{
                    max-height: 80px;
                }
                .footer__title {
                    font-family: Roboto;
                    font-size: 30px;
                    font-weight: 500;
                    line-height: 35px;
                    letter-spacing: 0em;
                    text-align: left;      
                    color: white;
                    max-width: 481px;              
                }
                .footer__descr {
                    font-family: Roboto;
                    font-size: 18px;
                    font-weight: 400;
                    line-height: 21px;
                    letter-spacing: 0em;
                    text-align: left;
                    color: white;
                    max-width: 454px;
                }
            }
            .college__contacts {
                .contacts__item {
                    font-family: Roboto;
                    font-size: 16px;
                    font-weight: 400;
                    line-height: 32px;
                    letter-spacing: 0em;
                    text-align: left;
                    color: white;
                }
                .contacts__item-bold {
                    font-weight: 700;
                    color: white;
                }
            }
        }
    }
}
.a-img {
    display: block;
    margin: 0px auto;
    margin-top: 100px;
    max-width: 300px;
    .footer-img {
        max-height: 300px;  
        display: block;
    }
}