$viola: #9900CC;
$viola2: #330066; 
$blue: #3300FF ;
$blue2: #31BAD0;

// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";
@import "text";

body {
    // background: black;
}
main {
    background: no-repeat center/100% url("../img/bg.svg");
    height: 95vh;
    .section__title {
        position: absolute;
        color: white;
        top: 40%;
        left: 50%;
        transform: translate( -50%, -50%);
        // font-family: DM Sans;
        font-size: 174px;
        font-weight: 700;
        line-height: 164px;
        letter-spacing: -0.04em;
        text-align: left;
        
    }
}

.section__title {
    font-family: Roboto;
    font-size: 55px;
    font-weight: 700;
    line-height: 69px;
    letter-spacing: -0.04em;
    text-align: left;
    color: white;
}
.section__descr {
    display: block;
    font-family: roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 31px;
    letter-spacing: -0.02em;
    text-align: left;
    color: white;
    max-width: 600px;
    margin-bottom: 50px;

}


.about {
    padding: 100px 0px;
    background: no-repeat center/100% url("../img/about-gradient.svg");
    .container {
        display: flex;
        justify-content: space-between;
        .about__text {

        }
        img {
            max-height: 400px;
            border-radius: 50%;
        }
    }
}
.news {
    padding: 100px 0px;
    background: black;
    .container {
        .section__title {
            span {
                margin-bottom: 20px;
            }
            display: flex;
            flex-direction: column;
            text-align: center;
            margin: 0px auto;
            img {
                
            }
        }
    }
    .container-grid {
                display: grid;
                justify-content: center;
                margin: 0px auto;
                margin-top: 60px;
                grid-template-columns: 440px 440px 440px;
                gap: 0px;
                .container__item {
                    padding: 40px 60px;
                    border: solid white 1px;
                    .item__img {
                        display: block;
                        margin: 0px auto;
                        height: 50px;
                    }
                    .item__title  {
                        font-family: Roboto;
                        font-size: 21px;
                        font-weight: 700;
                        line-height: 28px;
                        letter-spacing: 0px;
                        text-align: center;
                        color: white;
                    }
                    .item__descr {
                        font-family: Roboto;
                        color: white;
                        font-size: 16px;
                        font-weight: 400;
                        line-height: 26px;
                        letter-spacing: 0px;
                        text-align: center;

                    }
                }
                .container__item-active {
                    background: $blue2;
                }
        }   

}

.button {
    background: $viola2;
    color: white;
    border-radius: 40px;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 10px;
    padding-bottom: 10px;
    border: solid black;
    
}
// подключения компонентов страницы
@import "./components/header";
@import "./components/footer";



